import { NavDropdown } from "react-bootstrap";
import React from "react";
import Avatar from "@mui/material/Avatar";
import { useNavigate } from "react-router-dom";
import axios from "axios";
import styles from "./Header.module.scss";
import { LoginAPI } from "../../api/api";
export const DropDownUser = () => {
  const navigate = useNavigate();
  const LogOut = () => {
    localStorage.removeItem("token");
    navigate("/sign-in");
  };
  const user = {
    name: "Full Name",
    img: "data:image/jpeg;base64,",
  };

  const ImgAvatar = () => {
    return <Avatar sx={{ marginBottom: -2 }} alt={user.name} src={user.img} />;
  };

  return (
    <div className={styles.dropdownToggle}>
      <NavDropdown title={<ImgAvatar />} id="basic-nav-dropdown">
        <NavDropdown.Item href="/post" style={{ color: "red" }}>
          {user.name}
        </NavDropdown.Item>
        <NavDropdown.Item href="/post">Create Post</NavDropdown.Item>
        <NavDropdown.Divider />
        <NavDropdown.Item onClick={LogOut}>Log Out</NavDropdown.Item>
      </NavDropdown>
    </div>
  );
};
